.auth__submit-button {
  height: 50px;
  border: none;
  margin: 186px 0 0 0;
  padding: 0;
  color: var(--font-color-dark, #000);
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  border-radius: 2px;
  cursor: pointer;
  text-align: center;
  transition: opacity 0.2s linear;
}

.auth__submit-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 320px) {
  .auth__submit-button {
    margin: 143px 0 0 0;
    height: 46px;
    font-size: 16px;
    line-height: 19px;
  }
}