.popup__tooltip-header {
  margin: 0 0 26px 0;
  padding: 0;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
}

@media screen and (max-width: 618px) {
  .popup__tooltip-header {
    margin: 0 0 25px 0;
    font-size: 20px;
    line-height: 24px;
  }
}