.profile__user-name {
  grid-area: user-name;
  max-width: 294px;
  margin: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 618px) {
  .profile__user-name {
    max-width: 189px;
    font-size: 27px;
    line-height: 33px;
    text-align: center;
  }
}