.auth__link {
  padding: 0;
  margin: 15px 0 0;
  width: 100%;
  height: 17px;
  text-decoration: none;
  color: var(--font-color-light, #fff);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  cursor: pointer;
  transition: opacity 0.2s linear;
}

.auth__link:hover {
  opacity: 0.6;
}